.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form label {
  display: block;
}

.form input {
  width: 100%;
  padding: 1rem;
  box-sizing: border-box;
  border: 1px solid #ccc;
  border-radius: 0.6rem;
}

.form input:focus {
  border: 2px solid #0090e4;
  outline: none;
}